import React, { Component } from 'react'
import Container from './container'
export default class index extends Component {
    state={
        id:''
    }
    componentDidMount() {
        this.setState({
            id:this.props.formTaskId
        })
    }
    
    render() {
        return (
            <Container id={this.state.id}/>
        )
    }
}
